.santeh-list {
  display: none;
}

.active {
  background-color: $white-color;
  border-top: 1px solid $santeh-list-back;
  display: block;
  z-index: 11;
  position: absolute;
  left: 0;
  right: 0;
  height: 350px;
  transition: 0.9 s;
}

.santeh-list__shell {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.santeh-list__prewiev-img {
  width: 100%;
  height: 330px;
  object-fit: fill;
}

.santeh-list__nav  {
  padding: 40px 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.santeh-list__nav-common {
  padding-left: 20px;
  border-left: 1px solid $black-color;
  transition: 0.3px;
  display: none;

  li {
    padding-bottom: 8px;

    &:hover {
      color: $hover-color;
    }
  }
}

.santeh-list__nav-santeh {
  li {
    padding-bottom: 8px;
    transition: 0.3s;

    &:hover {
      color: $hover-color;
    }
  }
}



.santeh-list__nav-santeh-link {
  display: flex;
  flex-direction: row;
  max-width: 225px;
  align-items: baseline;
  cursor: pointer;

  .santeh-list__svg {
    display: none;
  }

}

.santeh-list__nav-santeh-link:hover,
.santeh-list__nav-santeh-link.styled  {
  color: $hover-color;

  .santeh-list__svg {
    display: block;
    margin-right: 10px;
  }
}

.santeh-list__prewiev-pict {
  display: none;
  object-fit: cover;
  width: 100%;
  height: 100%;
  padding-top: 20px;
}

.santeh-list__nav-common {
  li {
    cursor: pointer;
    transition: 0.3s;
  }
}
