.card-badge {
  height: 45px;
  color: $white-color;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;

  &--bests {
  width: 117px;
  background-color: $color-badges-bests;
  }

  &--new {
    width: 61px;
    background-color: $color-badges-new;
  }
}